// RESPONSIVE
@import "theme";

@media (max-width: 1199px) {
  #mu-slider .mu-slider-single .mu-slider-content {
    padding: 0 10%;
  }
  #mu-slider .mu-slider-single .mu-slider-content {
    top: 13%;
  }

  #mu-about-us .mu-about-us-area .mu-about-us-right {
    float: right;
    width: 48%;
  }

  #mu-footer
    .mu-footer-top
    .mu-footer-top-area
    .mu-footer-widget
    .mu-subscribe-form
    .mu-subscribe-btn {
    background-color: transparent;
  }
}

@media (max-width: 991px) {
