    .mu-our-teacher-single {
    margin-bottom: 30px;
  }
  #mu-testimonial .mu-testimonial-area {
    padding: 0 20px;
  }
  #mu-testimonial
    .mu-testimonial-area
    .mu-testimonial-content
    .mu-testimonial-item
    .mu-testimonial-quote {
    padding: 60px 15px;
  }
  #mu-testimonial
    .mu-testimonial-area
    .mu-testimonial-content
    .mu-testimonial-item
    .mu-testimonial-quote
    blockquote::before {
    left: 0;
  }
  #mu-from-blog {
    padding: 60px 0;
  }
  #mu-from-blog .mu-from-blog-area .mu-from-blog-content {
    margin-top: 30px;
  }
  #mu-from-blog
    .mu-from-blog-area
    .mu-from-blog-content
    .mu-blog-single-item
    .mu-blog-meta
    a {
    letter-spacing: 0;
    margin-right: 5px;
  }
  #mu-from-blog
    .mu-from-blog-area
    .mu-from-blog-content
    .mu-blog-single-item
    .mu-blog-single-img
    .mu-blog-caption
    h3 {
    font-size: 20px;
  }
  #mu-course-content .mu-course-content-area .mu-sidebar {
    margin-top: 30px;
  }
  #mu-search .mu-search-area .mu-search-form input[type="search"] {
    font-size: 30px;
  }
  #mu-search .mu-search-area .mu-search-close {
    right: 50px;
  }
  #mu-search .mu-search-area {
    padding: 35% 0;
  }
  #mu-contact .mu-contact-area .mu-contact-content .mu-contact-right {
    margin-top: 50px;
  }
}

@media (max-width: 767px) {
  .navbar-header {
    padding: 10px 0;
  }
  #mu-menu .navbar-header .navbar-brand {
    padding-top: 10px;
  }
  #mu-search-icon {
    display: none;
  }
  .navbar-toggle {
    background-color: $theme-color;
    border-radius: 0;
  }
  .navbar-default .navbar-toggle {
    border-color: $theme-color;
  }
  .navbar-default .navbar-toggle .icon-bar {
    background-color: #fff;
  }
  .navbar-default .navbar-toggle:hover,
  .navbar-default .navbar-toggle:focus {
    background-color: $theme-color;
  }
  #mu-menu .navbar-default .navbar-nav li > a {
    padding: 15px 20px;
  }
  #mu-slider .mu-slider-single .mu-slider-content h2 {
    font-size: 30px;
    line-height: 60px;
  }
  #mu-abtus-counter {
    background-position: center center;
    background-size: cover;
  }
  #mu-abtus-counter .mu-abtus-counter-area .mu-abtus-counter-single {
    border: medium none;
    margin-bottom: 25px;
  }
  #mu-features {
    .mu-features-area {
      .mu-features-content {
        .mu-single-feature {
          text-align: center;
          margin-left: 15px;
        }
      }
    }
  }
  #mu-features
    .mu-features-area
    .mu-features-content
    .mu-single-feature
    h4::after {
    display: none;
  }
  #mu-latest-courses
    .mu-latest-courses-area
    .mu-latest-courses-content
    .mu-latest-course-single
    .mu-latest-course-img
    .mu-latest-course-imgcaption
    a {
    font-size: 14px;
  }
  #mu-latest-courses
    .mu-latest-courses-area
    .mu-latest-courses-content
    .mu-latest-course-single
    .mu-latest-course-img
    .mu-latest-course-imgcaption
    span {
    font-size: 14px;
  }
  #mu-from-blog .mu-from-blog-area .mu-from-blog-content .mu-blog-single-item {
    margin-bottom: 30px;
  }
  #mu-footer .mu-footer-top .mu-footer-top-area .mu-footer-widget {
    margin-bottom: 30px;
    text-align: center;
  }
  #mu-footer
    .mu-footer-top
    .mu-footer-top-area
    .mu-footer-widget
    .mu-subscribe-form
    input[type="email"] {
    width: 50%;
    margin-right: 10px;
  }
  #about-video-popup iframe {
    height: 350px;
    width: 80%;
  }
  #about-video-popup span {
    position: absolute;
    right: 30px;
    top: 30px;
    margin-right: 0;
    margin-top: 0;
  }
  #mu-course-content {
    padding: 50px 0;
  }
  #mu-gallery {
    padding: 50px 0;
  }
  #mu-gallery
    .mu-gallery-area
    .mu-gallery-content
    .mu-gallery-body
    ul
    li
    .mu-single-gallery
    .mu-single-gallery-item
    .mu-single-gallery-img {
    height: 320px;
  }
  #mu-contact {
    padding: 50px 0;
  }
  #mu-error {
    padding: 50px 0;
  }
  #mu-error .mu-error-area h2 {
    font-size: 150px;
    line-height: 1.5;
  }
}

@media (max-width: 640px) {

  #mu-header .mu-header-area .mu-header-top-left .mu-top-phone {
    display: none;
  }
  #mu-slider .mu-slider-single .mu-slider-content h2 {
    font-size: 25px;
    line-height: 50px;
  }
  #mu-slider .mu-slider-single .mu-slider-img {
    height: 350px;
  }
  #mu-slider .mu-slider-single .mu-slider-content h4 {
    letter-spacing: 0;
  }

  #mu-service .mu-service-area .mu-service-single {
    padding: 20px 5px;
  }
  #mu-service .mu-service-area .mu-service-single h3 {
    font-size: 16px;
  }
  #mu-slider .mu-slider-single .mu-slider-img figure img {
    width: auto;
  }
  #mu-gallery .mu-gallery-area .mu-gallery-content .mu-gallery-top ul li {
    padding: 5px 10px;
    margin-bottom: 5px;
  }

	#mu-about-us .mu-about-us-area .mu-about-us-right {
    float: right;
    width: 48%;

		> img {
			width: 100%;
			height: 100%;
			align-items: center;
			object-fit: fill;
			border-radius: 2rem;
		}
  }

	#mu-mentoring .mu-mentoring-area .mu-mentoring-content {
		width: 100% !important;
	}
}

